.place {
  &__photo-container {
    position: relative;

    width: 100%;
    max-height: 256px;
    overflow: hidden;
    border-radius: 8px;

    transition: transform 0.5s, box-shadow 0.3s;

    &::after {
      content: "";
      display: block;
      padding-bottom: 100%;
    }

    &:hover {
      transform: scale(1.06);
      box-shadow: 0 0 20px #bababa;
    }

    @include onDesktop {
      height: 256px;
      padding-bottom: 0;
    }
  }

  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 8px;

    object-fit: cover;
    object-position: center;
  }

  &__photos {
    margin-bottom: 16px;

    @include onDesktop {
      margin-bottom: 24px;
    }
  }

  &__link {
    font-weight: bold;
    color: $colorBlue;
  }
}
